.main-view {
  padding: 20px;
  display: flex;
  flex-flow: row;
}

.sidebar { 
  /* width: 200px; */
}

.user-list {
  display: flex;
  flex-flow: row nowrap;
  /* align-items: center; */
  justify-content: flex-start;
  overflow: scroll;
}

.user-view {
  padding: 20px;
  font-size: 12px;
  position: relative;
  top: -250px;
  max-width: 200px;
}

.user-commit-info.owned-by-user {
  color: #DDA01E;
}

.user-commit-info {
  padding: 5px;
  height: 285px;
  /* border-width: 1px 1px;
  border-style: dashed;
  border-color: #130d3636; */

  border-width: 0px 1px 1px 0px;
  border-style: solid;
  border-color: #130d3636;
  border-radius: 10px;
  margin-bottom: 10px;

  display: flex;
  flex-flow: column;

  transition-duration: 250ms;
  transition-property: background-color, border-color;
}

.user-commit-info.new {
  align-items: center;
  justify-content: flex-start;
}

.user-commit-info:hover {
  background-color: #72A7E7a0;
  border-color: rgba(190, 208, 230, 0.627);
}

a {
  color: black;
  text-decoration: none; /* no underline */
}

/* .ibb {
  position: absolute;
} */

.username {
  font-size: 16px;
  width: 100%;
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.userlist-box {
  border-width: 1px;
  border-style: dotted;
  border-color: #130d3636;
  border-radius: 5px 20px 20px 5px;
  margin: 5px;
  background-color: #0096C7b0;
  width: 150px;
  left: 0px;

  display: flex;
  flex-flow: row;
  align-items: center;
  /* justify-content: flex-start; */

  transition-duration: 250ms;
  transition-property: background-color, border-color, border-radius, border-style, width, left;
}

.loading-userlist-box {
  width: 300px;
}

.selected-userlist-box {
  border-style: solid;
  border-color: #C6F8FF80;
  background-color: rgba(61, 161, 194, 1)
}

.userlist-box:hover {
  position: relative;
  left: 15px;
  border-style: solid;
  border-color: #C6F8FF80;
  border-radius: 5px;
  background-color: rgba(61, 161, 194, 1)
}

.userlist-title {
  font-size: 14px;
  font-weight: bold;
  padding-left: 5px;
}

.userlist-member {
  font-size: 10px;
  padding-left: 15px;
}

.loader-container {
  position: relative;
  left: 80px;
}

.conversation-message {
  border-width: 0px 0.5px 1px 0px;
  border-style: solid;
  border-color: #130d3636;
  border-radius: 6px;
  padding: 2px;
  margin-bottom: 2px;
}

html {
  height: 100%;
  background-image: linear-gradient(to top, #0547aa, #005ab1, #006cb4, #267cb5, #488bb4);
  background-size: cover;
  background-attachment: fixed;
}

.login-button {
  display: flex;
  align-self: center;
  margin-top: 300px;
}

.login-button a {
  border-style: groove;
  border-width: 3px;
  border-color: darkblue;
  background: white;
  border-radius: 5%;
  padding: 10px;
  margin: auto;
}

.error-message {
  margin: 5px;
  color: white;
  font-weight: bold;
  font-size: 15px;
}